/* Jost, for subheadings and paragraphs */
@import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100..900;1,100..900&display=swap');
@import './styles/variables.scss';

html {
  font-size: 62.5%;
  
  a {
    cursor: pointer;
  }
}

.inner-container {
  position: relative;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 100px;
  overflow: hidden;
}

.centered-text {
  text-align: center;
}

.p-vertical-100 {
  padding: 100px 0;
}

.primary-button {
  font-family: 'Jost', sans-serif;
  font-size: 3rem;
  font-weight: 300;
  text-decoration: none;
  background-color: $dark-gray;
  padding: 10px 25px;
  color: white;
  border-radius: 20px;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'DM Serif Display', serif;
}

h1 {
  font-size: 6rem;
}

h2 {
  font-size: 4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.5rem;
}

p {
  font-family: 'Jost', sans-serif;
  font-size: 2rem;
  font-weight: 300;
}

.white-highlight {
  background-color: white;
  color: $dark-gray;
  border-radius: 10px;
  padding: 2px 15px;
}

.pink-highlight {
  background-color: $pink;
  color: white;
  border-radius: 10px;
  padding: 2px 15px;
}

body {
  background-color: #f5f5f5;
  margin: 0;
  overflow-x: hidden;
  overflow-y: scroll;
  scroll-snap-type: y mandatory;
}

body, html {
  scroll-behavior: smooth;
  overflow-x: hidden; /* Prevent horizontal scrolling */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@media (max-width: 820px) {

}

@media (max-width: 390px) {
  .inner-container {
    padding: 50px 25px;
  }

  .primary-button {
    font-size: 2rem;
  }
}
